<template>
  <v-container>
    <v-row>
      <v-col cols="3">
        <v-btn @click="checkIfExists">Check</v-btn>
      </v-col>
      <v-col cols="9">
        <v-card tile>
          <iframe id="generator-frame" :src="autotestSrc" width="100%" height="100%"></iframe>
        </v-card>
      </v-col>
    </v-row>

  </v-container>
</template>

<script>

export default {
  name: "About",
  components: {
  },
  data() {
    return {
      autotestSrc: window.location.origin + '/panel/assets/autotestGenerator/html/index.html'
    }
  },
  mounted() {
    localStorage.setItem('.autotest-content', JSON.stringify({}));
    this.registerListener();
  },
  methods: {
    registerListener() {
      const button = document.getElementById("generator-frame").contentWindow.document.getElementById("export-button");
      if (button === null) {
        setTimeout(() => {
          this.registerListener();
        }, 1000);
      } else {
        button.addEventListener("click", ()=>{
          this.saveClicked();
        })
        console.log("successfully registered")
      }
    },
    saveClicked() {
      console.log("Save clicked");
      const conf = JSON.parse(localStorage.getItem('.autotest-content'));
      console.log(conf);
    }
  }
};
</script>
